import React from "react";
import {Button, Divider} from '@mui/material/';

import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';

import Typography from '@mui/material/Typography';


const ProductList = (props) => {

  const {products, addToBasket, onRemove} = props
  
  return (
    <div style={{ marginTop: "4em" }}>
      <div>
        <h3> Discounts </h3>
        <ul style={{ listStyle: "none", textAlign: "left" }}>
          <li>
            <b>Face Masks :</b> Two Face Masks for £4.
          </li>
          <li>
            <b>Toilet Paper :</b> Six rolls of toilet paper for the price of
            five.
          </li>
        </ul>
      </div>

      <h3 style={{ marginTop: "3em" }}>Product listings</h3>
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        {products.productOrders?.map((item) => (
          <Card key={item.id} sx={{ width: 255, height: "350px" }}>
            <CardMedia component="img" height="50%" image={item.img} />

            <CardContent>
              <Typography gutterBottom variant="p" component="div">
                <p>{item.name}</p>
                <p>£{item.price.toFixed(2)}</p>
              </Typography>
            </CardContent>

            <Divider variant="middle" />
            <CardActions style={{ float: "right" }}>
              <Button size="small" onClick={() => onRemove(item)}>
                Remove
              </Button>
              <Button onClick={() => addToBasket(item)} size="small">
                Add To Basket{" "}
              </Button>
            </CardActions>
          </Card>
        ))}
      </div>
    </div>
  );
};

export default ProductList;
